import React from "react";
import DeleteSweepIcon from "@mui/icons-material/DeleteSweep";

function Note(props) {
  function handleClick() {
    props.delete(props.id);
  }
  return(
  <div className="note">
    <h1>{props.title}</h1>
    <p>{props.content}</p>
    <button onClick={handleClick}>
      <DeleteSweepIcon />
    </button>
  </div>
)};

export default Note;
